/*Token.css*/
.token {
	z-index: 0;
	display: block;
	width: 100%;
	height: 100%;
	border-radius: 100%;
	-webkit-animation: animation 1200ms linear both;
	animation: animation 1200ms linear both;
}
.token.user {
	background-color: red;	
}
.token.cpu {
	background-color: blue;	
}

