/*Modal.css*/
.modal-container {
    position: absolute;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.4);
    top: 0;
    left: 0;
    z-index: 1000;
    transition: opacity ease 0.4s;
    opacity: 0;
    display: flex;
    display: -ms-flexbox;
    display: -webkit-flex;
}
.modal-container.open {
    opacity: 1;
}
.modal-container.closed {
    opacity: 0;
}
.modal {
    margin: auto;
    border: 1px solid gray;
    background-color: #fff;
    padding: 2em;
}
.modal > p {
    text-align: center;
}
.modal > p:first-child {
    font-size: 3em;
}