/*Column.css*/
.column {
	position: relative;
	display: flex;
	flex-direction: column;
	z-index: 1;
	justify-content: space-between;
}
.column.open {
	cursor: pointer;
}
.slot {
	z-index: 2;
	display: block;
	width: 4em;
	height: 4em;
	border-radius: 100%;
	border: 2px solid black;
}